___CSS_LOADER_EXPORT___.push([module.id, `body {
  height: 100vh;
  display: grid;
  grid-template-rows: 1fr 10fr;
}

.outer-nav {
  color: white;
  background: rebeccapurple;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.outer-nav ul {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
}

.outer-nav li {
  cursor: pointer;
}

.inner-nav {
}

/** **\`\` GIANT MEDIA QUERY \`\`** */
/** ***************************************************** */

/*todo **\`\` Mess with giant media query */

@media (width <= 400px) {
  .mobile-wrap {
    padding: 40px 0;
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .mobile-menu {
    width: 40px;
    height: 40px;
    border-radius: 0 10px 10px 0;
    box-shadow:
      2px 2px 5px rgba(0, 0, 0, 0.1),
      5px 5px 10px rgba(0, 0, 0, 0.3),
      -2px 2px 5px rgba(0, 0, 0, 0.1),
      -5px 5px 10px rgba(0, 0, 0, 0.3);
    background: radial-gradient(#146ef5, #0045ac);
  }

  .line1 {
    width: 12px;
    height: 1px;
    background: white;
    border-radius: 10px;
    position: relative;
    top: 15px;
    left: 10px;
  }

  .line2 {
    width: 12px;
    height: 1px;
    background: white;
    border-radius: 10px;
    position: relative;
    top: 22px;
    left: 14px;
  }

  .lower-menu-wrap {
    display: flex;
    justify-content: center;
    align-self: center;
    pointer-events: none;
  }

  .lower-menu-wrap.clicked .mobile-exit {
    translate: 0px 0px;
    pointer-events: initial;
  }

  .mobile-exit {
    translate: 0px 100px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    box-shadow:
      2px 2px 5px rgba(0, 0, 0, 0.1),
      5px 5px 10px rgba(0, 0, 0, 0.3),
      -2px 2px 5px rgba(0, 0, 0, 0.1),
      -5px 5px 10px rgba(0, 0, 0, 0.3);
    background: radial-gradient(#146ef5, #0045ac);
    color: white;
    font-size: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: translate 400ms cubic-bezier(0.51, 0.11, 0.22, 2.24) 400ms;
  }

  .shell-wrap {
    position: absolute;
  }

  .shell-wrap.clicked .outer-shell {
    transform: scale(10);
    top: -30px;
    left: -80px;
  }

  .shell-wrap.clicked .inner-shell {
    transform: scale(10);
    top: -30px;
    left: -140px;
  }

  .outer-shell {
    position: absolute;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    top: -90px;
    left: -150px;
    background: #146ef5;
    transition:
      transform 400ms ease-in-out,
      left 400ms ease-in-out,
      top 400ms ease-in-out;
  }

  .inner-shell {
    position: absolute;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    top: -90px;
    left: -150px;
    background: #0045ac;
    transition:
      transform 400ms ease-in-out 100ms,
      left 400ms ease-in-out 100ms,
      top 400ms ease-in-out 100ms;
  }

  .inner-nav {
    position: absolute;
    color: white;
    padding: 50px;
    /* z-index: -100; */
    pointer-events: none;
    /* background: rgb(57, 42, 42); */
    width: 100vw;
    height: 60vh;
  }

  .inner-nav.clicked .nav-ul {
    opacity: 1;
    pointer-events: initial;
  }

  .nav-ul {
    /* position: absolute; */
    list-style-type: none;
    font-size: 1.5rem;
    display: flex;
    flex-direction: column;
    /* background: blue; */
    justify-content: space-between;
    height: 100%;
    opacity: 0;
    pointer-events: none;
    transition: opacity 450ms ease-in-out;
  }
}

